@media (max-width: 767px) {

	.text-wrapper,
	.services-block,
	.contacts-map,
	.pros-tmb,
	.section-header {
		transform: none !important;
		opacity: 1 !important;
	}

}

.section-header {
	opacity: 0;
	transform: translateX(-100px);
}

.pros-tmb {
	opacity: 0;
	transform: translateX(-100px);
}

.text-wrapper {
	opacity: 0;
	transform: translateX(-100px);
}

.services-block {
	opacity: 0;
	transform: translateX(100px);
}

.custom-slider-wrapper {
	opacity: 0;
	transform: translateX(100px);
}

.contacts-map {
	opacity: 0;
	transform: scale(.5);
}