// Sliders

.slick-arrow {
	position: absolute;
	z-index: 10;
	border: none;
	-webkit-appearance: none;
	width: 64px;
	height: 64px;
	background: #CEAE81;
	border-radius: 50%;
	transition: all .2s ease;
	font-size: 0;
	line-height: 0;
	color: transparent;
	text-indent: -20000px;
	overflow: hidden;
	top: 50%;
	margin-top: -32px;
	cursor: pointer;
	outline: none !important;
}

.slick-arrow:before {
	content: "";
	width: 12px;
	height: 20px;
	background-repeat: no-repeat;
	background-position: 0 0;
	position: absolute;
	left: 50%;
	top: 50%;
	margin: -10px 0 0 -6px;
}

.slick-prev:before {
	background-image: url(../images/slick-prev.svg);
}

.slick-next:before {
	background-image: url(../images/slick-next.svg);
}

.slick-prev {
	left: 15px;
}

.slick-next {
	right: 15px;
}

