// Modals

.modal {
	padding: 0 !important;
}

.modal-dialog {
	max-width: initial;
	margin: 0;
	padding: 0;
	background: #fff;
	pointer-events: initial;
}

.modal-content {
	border-radius: 0;
	border: none;
	margin: 0;
	padding: 0;
}

.floor-modal .modal-dialog {
	min-height: 100%;
}

.floor-modal .modal-content {
	padding: 50px 15px;
	margin: 0;
	min-height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.floor-wrapper {
	max-width: 1250px;
	margin: 0 auto;
	width: 100%;
}

.modal .close {
	width: 22px;
	height: 22px;
	opacity: 1 !important;
	cursor: pointer;
	position: absolute;
	right: 20px;
	top: 10px;
	z-index: 10;
	outline: none !important;
}

.modal .close:before {
	content: "";
	transition: all .15s ease;
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: url(../images/close.svg) 0 0 no-repeat;
	transition: all .2s ease;
	opacity: 1;
}

.modal .close:after {
	content: "";
	transition: all .15s ease;
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: url(../images/close-hover.svg) 0 0 no-repeat;
	transition: all .2s ease;
	opacity: 0;
}

.modal .close:hover:before {
	opacity: 0;
}

.modal .close:hover:after {
	opacity: 1;
}

.map-modal .modal-dialog {
	height: 100%;
}

.map-modal .modal-content {
	height: 100%;
}

.modal-map {
	height: 100%;
}

.map-modal .close {
	width: 60px;
	height: 60px;
	opacity: 1 !important;
	background: #2A2723;
	border-radius: 50%;
}

.map-modal .close:before {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
