#md-indicator {
	display: none;
}

#mobile-indicator {
	display: none;
}

.navbar-trigger {
	display: none;
}

.navbar-wrapper .lang-selector {
	display: none;
}

// lg

@media (max-width: 1299px) {

	.btn-feedback,
	.btn-feedback:focus {
		width: 160px;
		padding: 24px 10px 26px 10px;
	}

	.header-col-2 {
		order: 3;
		flex: 0 0 100%;
		max-width: 100%;
	}

	.header-col-3 {
		order: 1;
		max-width: 140px;
	}

	.header-col-4 {
		order: 2;
	}

	.navbar-wrapper {
		height: auto;
		padding-top: 8px;
	}

	.lang-selector {
		height: auto;
		padding: 6px 0 0 37px;
	}

	.header-phone {
		height: auto;
		padding: 1px 0 0 0;
	}

	.pros-tmb {
		font-size: 16px;
		line-height: 22px;
	}

	.pros-floors-tmb {
		min-width: 120px;
	}

	.pros-num {
		font-size: 45px;
		line-height: 40px;
	}

	.pros-areas-tmb {
		padding-right: 45px;
	}

	.pros-areas-tmb:before {
		width: 30px;
	}

	.pros-areas .pros-num {
		font-size: 35px;
		line-height: 35px;
	}

	.infra-item-ttl {
		font-size: 18px;
		line-height: 28px;
	}

	.infra-item-content {
		font-size: 15px;
		line-height: 24px;
	}

	.plan-menu svg,
	.floor-menu svg {
		width: 34px;
	}

	ul.plan-menu {
		margin: 0 -10px;
	}

	ul.plan-menu > li {
		padding: 0 10px;
	}

	ul.plan-menu span, ul.floor-menu span {
		font-size: 24px;
		line-height: 28px;
		top: -3px;
		margin: 0 0 0 4px;
	}

	ul.plan-menu > li:after {
		left: 85px;
		right: -8px;
		top: 18px;
	}

	.services-list .col {
		flex: 0 0 50%;
		max-width: 50%;
	}

	.feedback-form-wrapper-inner {
		width: 840px;
		margin: 0 auto;
		background: #fff;
		padding: 96px 66px 66px 66px;
		position: relative;
	}

	.feedback-form-wrapper-inner:before {
		border-left: 420px solid transparent;
		border-right: 420px solid transparent;
	}

	.floor-plan-slider-wrapper {
		max-width: 602px;
	}

}

// lg END

// md

@media (max-width: 1119px) {

	#md-indicator {
		display: block;
	}

	html,
	body {
		font-size: 16px;
	}

	.over-wrapper {
		padding-left: 0;
		padding-right: 0;
	}

	.over-wrapper:before {
		display: none;
	}

	.over-wrapper:after {
		display: none;
	}

	header {
		height: 60px;
		padding: 0 120px 0 50px;
	}

	.btn-login {
		width: 50px;
		height: 60px;
	}

	.header-logo {
		margin: 25px 0 0 0;
	}

	.header-logo img {
		width: 80px;
	}

	.header-col-1 {
		flex: 0 0 110px;
		max-width: 110px;
	}

	.btn-feedback,
	.btn-feedback:focus {
		height: 60px;
		width: 120px;
		white-space: normal;
		text-align: left;
		padding: 9px 15px 0 15px;
	}

	.navbar-wrapper {
		display: none;
		padding: 0;
		position: fixed;
		background: #fff;
		top: 60px;
		right: 120px;
		padding: 10px 20px 10px 20px;
	}

	.header-col-3,
	.header-col-4 {
		height: 60px;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	.lang-selector {
		padding-top: 0;
		padding-bottom: 0;
	}

	.header-phone {
		padding-top: 0;
		padding-bottom: 0;
	}

	.header-content {
		height: 60px;
	}

	.custom-slider-wrapper.fixed {
		right: 0;
		position: absolute;
	}

	.custom-slider-wrapper {
		position: relative !important;
		left: auto !important;
		right: auto !important;
		top: auto !important;
		bottom: auto !important;
		width: 560px !important;
		height: 400px !important;
		margin: 0 auto 40px auto !important;
	}

	.custom-slider .slick-prev {
		left: -80px;
	}

	.custom-slider .slick-next {
		right: -80px;
	}

	.section {
		display: flex;
		flex-direction: column;
	}

	.section > .custom-slider-wrapper {
		order: 2;
	}

	.section > .container {
		order: 1;
	}

	.section-descr {
		width: auto;
		padding: 0;
	}

	.about-content-block {
		max-width: 100%;
	}

	.section-content {
		padding: 40px 0 40px 0;
	}

	.section-header h2, .section-header .h2 {
		font-size: 55px;
		line-height: 55px;
	}

	.contacts-map {
		width: 330px;
		height: 330px;
	}

	.section-location .section-header h2,
	.section-location .section-header .h2 {
		font-size: 60px;
		line-height: 60px;
	}

	.location-deco:after {
		display: none;
	}

	location-sub-ttl {
		font-size: 35px;
		line-height: 40px;
	}

	.location-descr-bottom {
		font-size: 18px;
		line-height: 28px;
	}

	.navbar-trigger {
		width: 30px;
		height: 30px;
		position: fixed;
		right: 150px;
		top: 14px;
		cursor: pointer;
		display: block;
	}

	.navbar-trigger span {
		position: absolute;
		left: 0;
		right: 0;
		height: 2px;
		background: #000;
		transition: all .2s ease;
	}

	.navbar-trigger span:nth-child(1) {
		top: 2px;
	}

	.navbar-trigger span:nth-child(2) {
		top: 50%;
		margin-top: -1px;
	}

	.navbar-trigger span:nth-child(3) {
		bottom: 2px;
	}

	.navbar-trigger.active span:nth-child(1) {
		top: 50%;
		margin-top: -1px;
		transform: rotate(45deg);
	}

	.navbar-trigger.active span:nth-child(2) {
		opacity: 0;
	}

	.navbar-trigger.active span:nth-child(3) {
		bottom: 50%;
		margin-bottom: -1px;
		transform: rotate(-45deg);
	}

	.section-infra .section-descr {
		max-width: 100%;
	}

	.plan-pic-wrapper {
		margin: 0 -100px;
	}

	.plan-menu svg, .floor-menu svg {
		width: 30px;
	}

	ul.plan-menu {
		margin-left: -5px;
		margin-right: -5px;
	}

	ul.plan-menu > li {
		padding: 0 5px;
	}

	ul.plan-menu > li:after {
		display: none;
	}

	ul.plan-menu span, ul.floor-menu span {
		font-size: 21px;
		top: -2px;
	}

	.floor-col-1 {
		flex: 0 0 100%;
		max-width: 100%;
	}

	.floor-col-2 {
		flex: 0 0 100%;
		max-width: 100%;
	}

	.floor-col-3 {
		flex: 0 0 100%;
		max-width: 100%;
	}

	ul.floor-menu {
		flex-direction: row;
		margin-left: -5px;
		margin-right: -5px;
		height: auto;
	}

	ul.floor-menu > li {
		padding: 0 5px;
		margin: 0;
	}

	ul.floor-menu > li:after {
		display: none;
	}

	.floor-plan-slider-wrapper {
		max-width: 100%;
	}

	.room-form {
		margin-top: 40px;
	}

	.rooms-list {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}

	.selected-room {
		flex: 0 0 50%;
		max-width: 50%;
		margin: 0 -1px -1px 0;
	}

	.room-form .fields-wrapper {
		max-width: 350px;
		margin-left: auto;
		margin-right: auto;
	}

	.room-col-1 {
		padding: 40px;
	}

	.room-descr {
		padding: 35px 40px 40px 40px;
	}

	.service-tmb-pic {
		width: 60px;
		height: 60px;
	}

	.service-tmb-descr {
		font-size: 15px;
		min-height: 60px;
	}

	.feedback-form-wrapper-inner {
		width: 690px;
	}

	.feedback-form-wrapper-inner:before {
		border-left: 345px solid transparent;
		border-right: 345px solid transparent;
	}

	.over-wrapper {
		padding-top: 60px;
	}

}

// md END

// sm

@media (max-width: 767px) {

	#mobile-indicator {
		display: block;
	}

	html,
	body {
		font-size: 14px;
	}

	.btn-3, .btn-3:focus {
		font-size: 18px;
		line-height: 22px;
		padding: 16px 20px 16px 20px;
	}

	.btn-2, .btn-2:focus {
		font-size: 18px;
		line-height: 22px;
		padding: 16px 20px 16px 20px;
	}

	.container {
		max-width: 100% !important;
	}

