		height: 44px;
		padding: 0;
		overflow: hidden;
	}

	.btn-feedback:before {
		content: "";
		width: 26px;
		height: 20px;
		position: absolute;
		left: 50%;
		top: 50%;
		margin: -10px 0 0 -13px;
		background: url(../images/btn-feedback-ico.svg) 0 0 no-repeat;
		background-size: 100% auto;
	}

