		margin: 0 0 15px 0;
	}

	.pros-floors-tmb:after {
		left: 60px;
		top: 15px;
	}

	.pros-areas-tmb {
		padding-right: 20px;
	}

	.pros-areas .pros-num {
		font-size: 22px;
		line-height: 22px;
	}

