		top: 14px;
	}

	.pros-tmb {
		margin-bottom: 30px;
	}

	.pros-list {
		margin-bottom: -30px;
	}

	.location-col-1 {
		flex: 0 0 100%;
		max-width: 100%;
	}

	.location-col-2 {
		flex: 0 0 100%;
		max-width: 100%;
	}

	.contacts-map-wrapper {
		justify-content: center;
		margin-top: 40px;
	}

	.location-descr-bottom {
		font-size: 16px;
		line-height: 28px;
	}

