		margin-bottom: -20px;
	}

	.service-tmb {
		margin: 0 0 20px 0;
	}

	.service-tmb-pic {
		width: 50px;
		height: 50px;
		padding: 10px;
	}

	.service-tmb-pic img {
		max-width: 100%;
		max-height: 100%;
	}

	.section-descr {
		min-height: 50px;
	}

	.room-form {
		margin-top: 30px;
	}

	.room-form h2, .room-form .h2 {
		font-size: 30px;
		line-height: 30px;
		margin: 0 0 15px 0;
	}

	.room-form-descr {
		font-size: 15px;
		line-height: 22px;
	}

	.selected-room {
		flex: 0 0 100%;
		max-width: 100%;
		padding: 13px 19px 19px 19px;
	}

	.selected-room .remove {
		right: 12px;
		top: 12px;
	}

	.selected-room h3, .selected-room .h3 {
		font-size: 18px;
	}

	.room-wrapper {
		flex-wrap: wrap;
	}

	.room-col-1 {
		flex: 0 0 100%;
		max-width: 100%;
		padding: 20px;
	}

	.room-col-2 {
		flex: 0 0 100%;
		max-width: 100%;
	}

	.room-col-2 {
		border-left: none;
		border-top: 1px solid #B2956D;
	}

	.room-descr {
		padding: 25px 20px 20px 20px;
	}

}

// sm END
